import React, {useEffect, useState} from 'react';
import {Container} from "@material-ui/core";
import {useSelector} from "react-redux";
import {useStyles} from "./StyleCurrencyExchange";
import {Header} from "./Header";
import {TabsPanelTitle} from "../SwitchTabs/TabsPanelTitle";
import {PageContent} from "./PageContent";


export function CurrencyExchange(props) {
	const classes = useStyles();
	const {currencies} = props
	const actual = useSelector(state => state.ActualTab.actual)

	const [state, setState] = useState();

	useEffect(() => {
		setState(currencies)
	}, [props]);

	return (
		 <Container className={actual === 0 ? classes.root_actual : classes.root} maxWidth={'lg'}>
			 <Container className={classes.content}>
				 <Header/>
				 <TabsPanelTitle/>
			 </Container>
			 <PageContent currencies={state ? state : currencies} setState={setState}/>
		 </Container>
	);
}
